/*----
    My crappy CSS:
    */
.form-control#notes {
  height: 200px;
}

.form-control.notes >text {
  rows: 12;
}

.form-control.double {
  height: 60px;
}

#wrapper{
  height: 100%;
}

.scrolling-table {
  overflow: scroll;
  height: 20%;
}

.col-centered{
    float: none;
    margin: 0 auto;
}

#sticky-footer
{
    position: absolute;
    width: 100%;
    height: 150px;
    z-index: 2500;
}

#calendar_box {
    position: relative;
    width: 100%;
    /*background: #D6D6D6;*/
    min-height: 400px;    
    z-index: 9999;
    top: 75px;
    padding: 15px;
    display: none;
}
.booked-schedule{
  background-color: rgba(31, 57, 181, 0.85);
  border-radius: 8px; 
  border: solid black 1px;
  box-shadow: 
    inset 0 2px 9px  rgba(255,255,255,0.3),
    inset 0 -2px 6px rgba(0,0,0,0.4);

}
.open-schedule{
    background-color: #f9f9f9;
}
.overlap{
    background-color: #FAAB3C;
}
#scheduler{
    *border: px solid #000;    
}
